import Vue from 'vue'
import App from './App'
import router from './router'
import axios from 'axios'
Vue.prototype.$axios = axios
Vue.config.productionTip = false
Vue.prototype.flat = function (val){
  if (!Array.isArray(val)) {
      return 
  } else {
      return val.join('、') 
  }
}
Vue.prototype.flatCast = function(cast){
  let arr = []
  if(Array.isArray(cast)){
      cast.forEach((item)=>{
          arr.push(item.name)
      })
      return arr.join('、')
  }else{
      return cast
  }
}
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  render: h => h(App),

})




