<template>
    <footer>
          <div @click="didClickedItem(0)" >      
            <router-link  to="/top250" v-bind:class="{active: actives[0]}">
              <span class="iconfont icon-guanjun"></span><span>top250</span>
            </router-link>
          </div>
          <div @click="didClickedItem(1)">
            <router-link to="/usBox" v-bind:class="{active: actives[1]}">
              <span class="iconfont icon-bangdan"></span><span>北美</span>
            </router-link>
          </div>
          <div @click="didClickedItem(2)" >
            <router-link to="/search" v-bind:class="{active: actives[2]}">
              <span class="iconfont icon-sousuo"></span><span>搜索</span>
            </router-link>
          </div>
    </footer>
</template>
<script>
    export default {
        data () {
            return {
                actives: [true, false, false]
            }
        },
        methods: {
            didClickedItem: function (tag) {
                if (tag === 0) {
                    this.actives = this.actives.map(function () {
                        return false;
                    });
                    this.actives[0] = true;
                } else if (tag === 1) {
                    this.actives = this.actives.map(function () {
                        return false;
                    });
                    this.actives[1] = true;
                } else if (tag === 2) {
                    this.actives = this.actives.map(function () {
                        return false;
                    });
                    this.actives[2] = true;
                }
            }
        }
    }
</script>



