import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path:'/',
      component:() => import ('@/pages/top250/top250.vue')
    },
    {
      path:'/usBox',
      component:() => import ('@/pages/usBox/usBox.vue')
    },
    {
      path:'/search',
      component:() => import ('@/pages/search/search.vue')
    },
    {
      path:'/top250',
      component:() => import ('@/pages/top250/top250.vue')
    }
  ]
})
export default router


